:root{

    /* ========== Color ========== */
    --Primary-color : hsl(199, 100%, 33%);
    --Secondary-color: hsl(187, 85%, 43%);
    --Gradient-color : linear-gradient(to bottom, var(--Secondary-color), var(--Primary-color));
    --white-color1: #FAF3F0;
    --White-color: #FFEEF4;
    --Black-color: #321E1E;
    --Text-color : #472D2D;
    --Body-color : #E7F6F2;
    --Card-bg : #D2E9E9;
	--Card-bg-1 : #4F6F52;

    /*========== Font weight ==========*/
	--font-medium: 500;
	--font-semi-bold: 600;

	/*========== Margenes Bottom ==========*/
	--mb-0-25: 0.25rem;
	--mb-0-5: 0.5rem;
	--mb-0-75: 0.75rem;
	--mb-1: 1rem;
	--mb-1-25: 1.25rem;
	--mb-1-5: 1.5rem;
	--mb-2: 2rem;
	--mb-2-5: 2.5rem;

	/*========== Shadow ==========*/
	--shadow-1: -2px 4px 10px hsla(0, 0%, 0%, 0.25);
   
	/*========== z index ==========*/
	--z-fixed: 100;
}



.home{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 8rem;

	.overlay{
		position: absolute;
		height: 100%;
		width: 100%;
		background: #4F6F52;
		top: 0;
		bottom: 0;
		z-index: 1;
		mix-blend-mode: hard-light;
	}

	video{
		position: absolute;
		height: 100%;
		top: 0;
		bottom: 0;
		object-fit: cover;
	}

	.home__content{
		padding: 7rem 1.5rem 3rem;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		color: var(--White-color);
		z-index: 99;

		.home__text{
			line-height: 1.4;
			text-align: center;

			.home__title{
				font-size: 1.8rem;
				font-weight: var(--font-semi-bold);
				padding-bottom: var(--mb-0-75);
			}

			.home__teks{
				font-size: var(--mb-1);
				padding-bottom: var(--mb-0-75);
			}
		
			.home__btn{
			margin-top: var(--mb-0-75);
			width: 100px;
            height: 50px;
            border-radius: 10px;
            border: 0;
			z-index: 11;
            background: var(--Primary-color);
            font-weight: var(--font-semi-bold);
            cursor: pointer;
			color: var(--White-color);
            
			&:hover{
                background-color: var(--Secondary-color);
                color: var(--White-color);
				}
			}
		}
	}

}

// ========== MEDIA QUERIES (RESPONSIVE HOME) ========== //

// for small device
@media screen and (min-width: 414px) {
.home{
	margin-bottom: 10rem;
	padding-bottom: 10rem;
}
}
// for medieum device
@media screen and (min-width: 568px) {

}

// for large device
@media screen and (min-width: 768px) {
	.home__content{
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
	}
}

// for desktop device
@media screen and (min-width: 1024px) {
	.home{
		width: 100%;
		height: 80vh;
	}
}

@media screen and (min-width: 1324px){
	.home{
		height: 85vh;
		position: relative;
		margin-bottom: -10rem;

	}
	video{
		height: 85vh;
		width: 100%;
	}

}