:root{

    /* ========== Color ========== */
    --Primary-color : hsl(199, 100%, 33%);
    --Secondary-color: hsl(187, 85%, 43%);
    --Gradient-color : linear-gradient(to right, var(--Secondary-color), var(--Primary-color));
    --White-color: #FFEEF4;
    --Black-color: #321E1E;
    --Text-color : #472D2D;
    --Body-color : #E7F6F2;
    --Card-bg : #D2E9E9;
    --Card-bg-1 : #A7D397;
    --container-color: hsl(212, 42%, 20%);

    /*========== Font weight ==========*/
	--font-medium: 500;
	--font-semi-bold: 600;

	/*========== Margenes Bottom ==========*/
	--mb-0-25: 0.25rem;
	--mb-0-5: 0.5rem;
	--mb-0-75: 0.75rem;
	--mb-1: 1rem;
	--mb-1-25: 1.25rem;
	--mb-1-5: 1.5rem;
	--mb-2: 2rem;
	--mb-2-5: 2.5rem;

	/*========== Shadow ==========*/
	--shadow-1: -4px 8px 24px hsla(0, 0%, 0%, 0.25);
   
	/*========== z index ==========*/
	--z-fixed: 100;

    /*========== Hover overlay ==========*/
    --img-transition: 0.3s;
    --img-hidden: hidden;
    --img-scale: scale(1.1);

}

.contact{
    padding-top: 6rem;
    padding-bottom: 5rem;
    

    .contact__data{
        
        h1{
        font-size: var(--mb-2);
        font-weight: var(--font-semi-bold);
        color: var(--Text-color);
        margin-bottom: var(--mb-0-75);
        padding: 10px;
        text-align: center;
        }
    }

    .contact__box{
        position: relative;
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-template-rows: 5fr 4fr;
        grid-template-areas: "form info" "form map";
        grid-gap: 20px;
        padding: 40px;
        margin: auto;
        width: 82%;

        

        .box{
            padding: 40px;
            background-color: var(--Card-bg);
            box-shadow: var(--shadow-1);
            border-radius: 10px;
        }

        .contact__form{
            grid-area: form;

            h3{
                font-size: 1.3rem;
                margin-bottom: var(--mb-0-75);
                color: var(--Text-color);
                display: flex;
                justify-content: start;
                align-items: center;
            }
            
            form{
                

                .form__box{
                    position: relative;
                    width: 80%;
                    

                    .form__box ,.row50{
                        display: flex;
                        gap: 20px;

                        .form__input{
                            display: flex;
                            flex-direction: column;
                            margin-bottom: 10px;
                            width: 50%;

                            span{
                                color: var(--Text-color);
                                font-weight: 500;
                            }

                            input{
                                padding: 10px;
                                font-size: 0.9rem;
                                outline: none;
                                color: var(--Text-color);
                                border-radius: 10px;
                                border: 0px solid --Black-color;
                            }
                        }                 
                    }

                    .form__box ,.row100{
                        display: flex;
                        gap: 20px;

                        .form__input{
                            display: flex;
                            flex-direction: column;
                            margin-bottom: 10px;
                            width: 100%;

                            span{
                                color: var(--Text-color);
                                font-weight: 500;
                            }

                            input{
                                padding: 10px;
                                font-size: 0.9rem;
                                outline: none;
                                color: var(--Text-color);
                            }

                            textarea{
                                padding: 10px;
                                font-size: 0.9rem;
                                outline: none;
                                color: var(--Text-color);
                                resize: none;
                                min-height: 220px;
                                border-radius: 10px;
                            }
                            
                            .input__btn{
                                background-color: var(--Card-bg-1);
                                border: none;
                                color: var(--Text-color);
                                font-size: 1rem;
                                max-width: 120px;
                                font-weight: 500;
                                cursor: pointer;
                                border-radius: 5px;

                                &:hover{
                                    background-color: var(--Primary-color);
                                    color: var(--White-color);
                                    
                                }
                            }

                            &::placeholder{
                                color: #D2E9E9;
                            }
                        }  
                    }
                }
            }
        }

        .contact__info{
            grid-area: info;
            background: var(--container-color);
            
            h3{
                font-size: 1.3rem;
                margin-bottom: var(--mb-0-75);
                color: var(--White-color);
            }

            .info__data{

                div{
                    display: flex;
                    align-items: center;
                    margin-bottom: 10px;

                    span{
                        min-width: 40px;
                        height: 40px;
                        color: var(--White-color);
                        background-color: var(--Primary-color);
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border-radius: 50%;
                    }

                    p{
                        color: var(--White-color);
                        padding-left: 10px;
                    }

                    a{
                        color: var(--White-color);
                        padding-left: 10px;
                    }
                }

            .social__media{
                    margin-top:40px ;
                    display: flex;

                    li{
                        margin-right: 15px;

                        a{
                            color: var(--White-color);
                            font-size: 20px;

                            &:hover{
                                color: var(--Primary-color);
                            }
                        }
                    }
                }
            }
        }

        .contact__map{
            grid-area: map;
            margin: 0;
            padding: 0;
            
            iframe{
                width: 100%;
                height: 100%;
                border-radius: 10px;
            }
        }
    }

    // Responsive
    @media (max-width: 991px){
            
            .contact__box{
                position: relative;
                display: grid;
                grid-template-columns: 1fr;
                grid-template-rows: auto;
                grid-template-areas: "form" "info" "map";
    
    
                .form__box , .row50{
                    display: flex;
                    gap: 0;
                    flex-direction: column;
                    justify-content: center;
                }
                
                .contact__form {
                    form {
                        .form__box {
                            .form__box {
                                .form__input {
                                    width: 100%;
                                }
                            }
                            .row50 {
                                .form__input {
                                    width: 100%;
                                }
                            }
                        }
                    }
                }
    
                .contact__map{
                    min-height: 300px;
                }
            }
        }

    @media (max-width: 595px){
        .contact{
            margin: auto;
            justify-content: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            
            
        }

        
        .contact__form{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
               
    
                h3{
                    padding-bottom: var(--mb-1-5);
                }
    
                form{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 100%;
                }
                
            }
        

        
    }
        
}