:root {
  /* ========== Color ========== */
  --Primary-color: hsl(199, 100%, 33%);
  --Secondary-color: hsl(187, 85%, 43%);
  --Gradient-color: linear-gradient(to right, var(--Secondary-color), var(--Primary-color));
  --White-color: #FFEEF4;
  --Black-color: #321E1E;
  --Text-color: #472D2D;
  --Body-color: #E7F6F2;
  --Card-bg: #D2E9E9;
  --Card-bg-1: #A7D397;
  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
  /*========== Margenes Bottom ==========*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-25: 1.25rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  /*========== Shadow ==========*/
  --shadow-1: -4px 8px 24px hsla(0, 0%, 0%, 0.25);
  /*========== z index ==========*/
  --z-fixed: 100;
  /*========== Hover overlay ==========*/
  --img-transition: 0.3s;
  --img-hidden: hidden;
  --img-scale: scale(1.1);
}

.popular {
  padding-top: 6rem;
  padding-bottom: 5rem;
}
.popular .data__popular {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: var(--mb-2);
}
.popular .data__popular h1 {
  font-size: var(--mb-2);
  font-weight: var(--font-semi-bold);
  color: var(--Text-color);
  margin-bottom: var(--mb-0-75);
  padding: 10px;
}
.popular .popular__destination .popular__card {
  position: relative;
  border-radius: 10px;
  width: 280px;
  height: 400px;
  overflow: var(--img-hidden);
}
.popular .popular__destination .popular__card .img__popular {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;
}
.popular .popular__destination .popular__card:hover {
  transform: translateY(20px);
}
.popular .popular__destination .popular__card:hover::before {
  opacity: 1;
}
.popular .popular__destination .popular__card:hover .popular__detail {
  opacity: 1;
  transform: translateY(0px);
}
.popular .popular__destination .popular__card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background: rgba(0, 0, 0, 0.6);
  z-index: 2;
  transition: 0.5s;
  opacity: 0;
}
.popular .popular__destination .popular__card .popular__detail {
  position: relative;
  z-index: 3;
  color: white;
  opacity: 0;
  transform: translateY(30px);
  transition: 0.5s;
  line-height: 1.5;
  padding: 10px;
}
.popular .popular__destination .popular__card .popular__detail:hover {
  transform: rotateX(0deg);
}
.popular .popular__destination .popular__card .popular__detail h3 {
  color: var(--White-color);
  font-weight: var(--font-semi-bold);
}
@media screen and (min-width: 495px) {
  .popular .popular__destination .popular__card {
    width: 100%;
    height: 420px;
  }
}
@media screen and (min-width: 568px) {
  .popular .popular__destination .popular__card {
    height: 450px;
  }
}
@media screen and (min-width: 768px) {
  .popular .popular__destination .popular__card {
    height: 500px;
    width: 320px;
  }
}
@media screen and (min-width: 1024px) {
  .popular .popular__destination .popular__card {
    height: 520px;
    width: 340px;
  }
}/*# sourceMappingURL=popular.css.map */