:root {
  /* ========== Color ========== */
  --Primary-color: hsl(199, 100%, 33%);
  --Secondary-color: hsl(187, 85%, 43%);
  --Gradient-color: linear-gradient(to right, var(--Secondary-color), var(--Primary-color));
  --White-color: #FFEEF4;
  --Black-color: #321E1E;
  --Text-color: #472D2D;
  --Body-color: #E7F6F2;
  --Card-bg: #D2E9E9;
  --Card-bg-1: #A7D397;
  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
  /*========== Margenes Bottom ==========*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-25: 1.25rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  /*========== Shadow ==========*/
  --shadow-1: -4px 8px 24px hsla(0, 0%, 0%, 0.25);
  /*========== z index ==========*/
  --z-fixed: 100;
  /*========== Hover overlay ==========*/
  --img-transition: 0.3s;
  --img-hidden: hidden;
  --img-scale: scale(1.1);
}

.destination .destination__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  padding-bottom: var(--mb-2-5);
}
.destination .destination__content h1 {
  font-size: var(--mb-2);
  font-weight: var(--font-semi-bold);
  color: var(--Text-color);
  padding-bottom: var(--mb-0-75);
}
.destination .destination__content .filter-list button {
  font-size: 1rem;
  border: none;
  cursor: pointer;
  color: var(--Text-color);
  font-weight: 500;
}
.destination .destination__content .filter-list button:hover {
  font-weight: var(--font-semi-bold);
  border-bottom: 1px solid var(--Primary-color);
}
.destination .cards__destination {
  display: grid;
  row-gap: 3.5rem;
  justify-content: center;
  align-items: center;
}
.destination .cards__destination .card__destination {
  width: 280px;
  height: 360px;
  border-radius: 15px;
  padding: 1.5rem;
  background: white;
  position: relative;
  display: flex;
  align-items: flex-end;
  transition: 0.4s ease-out;
  box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.5);
}
.destination .cards__destination .card__destination:hover {
  transform: translateY(20px);
}
.destination .cards__destination .card__destination:hover::before {
  opacity: 1;
}
.destination .cards__destination .card__destination:hover .destination__data {
  opacity: 1;
  transform: translateY(0px);
}
.destination .cards__destination .card__destination::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background: rgba(0, 0, 0, 0.6);
  z-index: 2;
  transition: 0.5s;
  opacity: 0;
}
.destination .cards__destination .card__destination img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 15px;
}
.destination .cards__destination .card__destination .destination__data {
  position: relative;
  z-index: 3;
  color: white;
  opacity: 0;
  transform: translateY(30px);
  transition: 0.5s;
  line-height: 1.5;
}
.destination .cards__destination .card__destination .destination__data:hover {
  transform: rotateX(0deg);
}
.destination .cards__destination .card__destination .destination__data h3 {
  color: var(--White-color);
  font-weight: var(--font-semi-bold);
}
.destination .cards__destination .card__destination .destination__data .destination__detail {
  margin-bottom: var(--mb-0-5);
}
.destination .cards__destination .card__destination .destination__data .destination__detail .location {
  padding-right: 5px;
}
.destination .cards__destination .card__destination .destination__data .destination__detail span {
  padding-left: 10px;
  font-size: 14px;
}
.destination .cards__destination .card__destination .destination__data .destination__detail p {
  font-size: 14px;
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn {
  align-items: center;
  justify-content: space-between;
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: transparent;
  padding: 0;
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn button:hover .circle {
  width: 100%;
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn button:hover .circle .icon.arrow {
  background: #fff;
  transform: translate(1rem, 0);
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn button:hover .button-text {
  color: var(--White-color);
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn .destination__fees {
  padding-left: 1rem;
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn .see-map {
  width: 11rem;
  height: auto;
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn .see-map .circle {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: relative;
  display: block;
  margin: 0;
  width: 3rem;
  height: 3rem;
  background: var(--Primary-color);
  border-radius: 1.625rem;
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn .see-map .circle .icon {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  background: #fff;
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn .see-map .circle .arrow {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  left: 0.625rem;
  width: 1.125rem;
  height: 0.125rem;
  background: none;
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn .see-map .circle .arrow::before {
  position: absolute;
  content: "";
  top: -0.29rem;
  right: 0.0625rem;
  width: 0.625rem;
  height: 0.625rem;
  border-top: 0.125rem solid #fff;
  border-right: 0.125rem solid #fff;
  transform: rotate(45deg);
}
.destination .cards__destination .card__destination .destination__data .destiantion__btn .see-map .button-text {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0.75rem 0;
  margin: 0 0 0 1.85rem;
  color: var(--White-color);
  font-weight: 700;
  line-height: 1.6;
  text-align: center;
}
@media screen and (min-width: 495px) {
  .destination .destination__content .filter-list button {
    font-size: 1.1rem;
    margin-left: 1rem;
  }
  .destination .card__destination {
    width: 350px;
    height: auto;
  }
  .destination .card__destination .destination__info {
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 0.5fr;
    justify-content: space-around;
  }
}
@media screen and (min-width: 568px) {
  .destination .destination__content .filter-list button {
    font-size: 1.2rem;
  }
  .destination .card__destination {
    width: 400px;
  }
}
@media screen and (min-width: 1024px) {
  .destination .cards__destination {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .destination .cards__destination .card__destination {
    width: 320px;
    height: 320px;
  }
  .destination .cards__destination .card__destination .destination__info p {
    font-size: 1.2rem;
    font-weight: var(--font-semi-bold);
  }
}/*# sourceMappingURL=destination.css.map */