:root {
  /* ========== Color ========== */
  --Primary-color: hsl(199, 100%, 33%);
  --Secondary-color: hsl(187, 85%, 43%);
  --Gradient-color: linear-gradient(to right, var(--Secondary-color), var(--Primary-color));
  --White-color: #FFEEF4;
  --Black-color: #321E1E;
  --Text-color: #472D2D;
  --Body-color: #E7F6F2;
  --Card-bg: #D2E9E9;
  --Card-bg-1: #A7D397;
  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
  /*========== Margenes Bottom ==========*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-25: 1.25rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  /*========== Shadow ==========*/
  --shadow-1: -4px 8px 24px hsla(0, 0%, 0%, 0.25);
  /*========== z index ==========*/
  --z-fixed: 100;
  /*========== Hover overlay ==========*/
  --img-transition: 0.3s;
  --img-hidden: hidden;
  --img-scale: scale(1.1);
}

.about {
  padding-top: 6rem;
  padding-bottom: 5rem;
  line-height: 1.5;
}
.about .about__content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.about .about__content span {
  font-family: "Kalam", cursive;
  color: var(--Card-bg-1);
  font-weight: 700;
  padding-bottom: var(--mb-1);
}
.about .about__content h1 {
  font-size: var(--mb-2);
  font-weight: var(--font-semi-bold);
  color: var(--Text-color);
  padding-bottom: var(--mb-0-75);
}
.about .about__content p {
  color: var(--Text-color);
}
.about .about__content .about__btn {
  width: 100px;
  height: 50px;
  border-radius: 10px;
  border: 0;
  background-color: var(--Card-bg-1);
  font-weight: var(--font-semi-bold);
  cursor: pointer;
}
.about .about__content .about__btn:hover {
  background-color: var(--Primary-color);
  color: var(--White-color);
}
.about .about__img1, .about .about__img {
  width: 130px;
  overflow: var(--img-hidden);
  transition: var(--img-transition);
  border-radius: 10px;
}
.about .about__img1:hover, .about .about__img:hover {
  transform: var(--img-scale);
}

@media screen and (max-width: 495px) {
  .about {
    margin-top: 12rem;
    padding-top: 10rem;
  }
  .about .about__img__content {
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .about .about__img__content .about__img1, .about .about__img__content .about__img {
    width: 200px;
  }
}
@media screen and (max-width: 495px) {
  .about__img__content {
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .about__img__content .about__img1, .about__img__content .about__img {
    width: 250px;
  }
}
@media screen and (min-width: 568px) {
  .about__img__content {
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .about__img__content .about__img1, .about__img__content .about__img {
    width: 270px;
  }
}
@media screen and (min-width: 768px) {
  .about__img__content {
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .about__img__content .about__img1, .about__img__content .about__img {
    width: 300px;
  }
}
@media screen and (min-width: 1024px) {
  .about {
    padding-top: 0;
  }
  .about__content {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-evenly;
    align-items: center;
    gap: 5rem;
    margin: auto;
  }
  .about__content .about__text {
    text-align: start;
  }
  .about__content .about__img__content {
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .about__content .about__img__content .about__img1, .about__content .about__img__content .about__img {
    width: 180px;
  }
}
@media screen and (min-width: 1324px) {
  .about {
    padding-top: 15rem;
  }
  .about__content {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-evenly;
    align-items: center;
    margin: auto;
  }
  .about__content .about__text {
    width: 100%;
    margin-left: 6rem;
  }
  .about__content .about__img__content {
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .about__content .about__img__content .about__img1, .about__content .about__img__content .about__img {
    width: 250px;
  }
}/*# sourceMappingURL=about.css.map */