:root {
  /* ========== Color ========== */
  --Primary-color: hsl(199, 100%, 33%);
  --Secondary-color: hsl(187, 85%, 43%);
  --Gradient-color: linear-gradient(to right, var(--Secondary-color), var(--Primary-color));
  --white-color1: #FAF3F0;
  --White-color: #FFEEF4;
  --Black-color: #321E1E;
  --Text-color: #472D2D;
  --Body-color: #E7F6F2;
  --Card-bg: #D2E9E9;
  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
  /*========== Margenes Bottom ==========*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-25: 1.25rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  /*========== Shadow ==========*/
  --shadow-1: -2px 4px 10px hsla(0, 0%, 0%, 0.25);
  /*========== z index ==========*/
  --z-fixed: 100;
}

.header {
  position: fixed;
  background: var(--white-color1);
  width: 100%;
  padding-top: 1rem;
  padding-bottom: 1rem;
  z-index: var(--z-fixed);
  box-shadow: var(--shadow-1);
  justify-content: center;
  align-items: center;
}
.header .logo__div {
  padding-left: 1.5rem;
}
.header .logo {
  color: var(--Black-color);
  font-weight: var(--font-semi-bold);
  cursor: pointer;
  align-items: center;
  justify-content: center;
}
.header .icon {
  color: var(--Primary-color);
  font-size: 25px;
  font-weight: var(--font-semi-bold);
  transform: translateY(5px);
}
.header .toggle__navbar {
  padding-right: 1.5rem;
}
.header .toggle__navbar .icon {
  font-size: 25px;
  color: var(--Primary-color);
}
.header .toggle__navbar:hover.icons {
  color: var(--Secondary-color);
}

@media screen and (max-width: 768px) {
  .container__navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 90%;
  }
  .navbar {
    position: absolute;
    right: 50%;
    transform: translate(50%);
    background: var(--white-color1);
    height: -moz-max-content;
    height: max-content;
    width: 80%;
    border-radius: 1rem;
    top: -66rem;
    z-index: 102;
    box-shadow: var(--shadow-1);
    transition: 0.3s ease-in-out;
    padding: 0.5rem;
  }
  .nav__list {
    display: flex;
    flex-direction: column;
  }
  .nav__item {
    padding-top: var(--mb-1);
  }
  .nav__btn {
    margin-top: var(--mb-1);
  }
  .nav__link {
    font-weight: var(--font-semi-bold);
  }
  .nav__link:hover {
    color: var(--Primary-color);
  }
  .close__navbar {
    position: absolute;
    top: 0.8rem;
    right: 1.2rem;
    font-size: 1rem;
    color: var(--Primary-color);
  }
  .close__navbar:hover {
    color: var(--Secondary-color);
  }
  .active__navbar {
    top: 7rem;
  }
}
@media screen and (min-width: 769px) {
  .container__navbar {
    display: flex;
    justify-content: space-between;
    gap: 5rem;
  }
  .nav__list {
    padding-right: var(--mb-1-5);
  }
  .nav__list .nav__item {
    padding-right: var(--mb-1-25);
  }
  .close__navbar, .toggle__navbar {
    display: none;
  }
}
@media screen and (min-width: 1024px) {
  .container__navbar {
    display: flex;
    width: 978px;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    align-self: center;
  }
  .container__navbar .logo__div {
    margin-bottom: 5px;
  }
  .container__navbar .nav__link {
    font-weight: 400;
  }
  .container__navbar .nav__link:hover {
    font-weight: var(--font-semi-bold);
  }
}
@media screen and (min-width: 1324px) {
  .container__navbar {
    width: 1200px;
  }
}/*# sourceMappingURL=navbar.css.map */