:root{

    /* ========== Color ========== */
    --Primary-color : hsl(199, 100%, 33%);
    --Secondary-color: hsl(187, 85%, 43%);
    --Gradient-color : linear-gradient(to right, var(--Secondary-color), var(--Primary-color));
    --White-color: #FFEEF4;
    --Black-color: #321E1E;
    --Text-color : #472D2D;
    --Body-color : #E7F6F2;
    --Card-bg : #D2E9E9;
    --Card-bg-1 : #A7D397;
    --container-color: hsl(212, 42%, 20%);

    /*========== Font weight ==========*/
	--font-medium: 500;
	--font-semi-bold: 600;

	/*========== Margenes Bottom ==========*/
	--mb-0-25: 0.25rem;
	--mb-0-5: 0.5rem;
	--mb-0-75: 0.75rem;
	--mb-1: 1rem;
	--mb-1-25: 1.25rem;
	--mb-1-5: 1.5rem;
	--mb-2: 2rem;
	--mb-2-5: 2.5rem;

	/*========== Shadow ==========*/
	--shadow-1: -4px 8px 24px hsla(0, 0%, 0%, 0.25);
   
	/*========== z index ==========*/
	--z-fixed: 100;

    /*========== Hover overlay ==========*/
    --img-transition: 0.3s;
    --img-hidden: hidden;
    --img-scale: scale(1.1);

}



.testimonial{
    padding-top: 6rem;
    padding-bottom: 5rem;

    .testimonial__data{
        font-size: var(--mb-2);
        font-weight: var(--font-semi-bold);
        color: var(--Text-color);
        margin-bottom: var(--mb-0-75);
        padding: 10px;
        text-align: center;
    }

    .cards__testimonial{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        margin: auto;

        .slider{
            position: relative;
            width: 70%;
        }

        .slick-slide > div{
            margin: 0 15px;
            
        }

        .card__article {
            width: 300px; 
            border-radius: 1.25rem;
            overflow: hidden;
            margin: 10px;
    
            .card__image {
                position: relative;
                padding-top: 1.5rem;
                margin-bottom: -.75rem;
    
                .card__img {
                    width: 100%;
                    margin: 0 auto;
                    position: relative;
                    z-index: 5;
                    border-radius: 10px 10px 0 0;
                }
            }
        }
    
        .card__data {
            background-color: var(--container-color);
            padding: 1.5rem 2rem;
            border-radius: 1rem;
            text-align: center;
            position: relative;
            height: 200px;
            bottom: 10px;
            z-index: 10;
    
            .card__name {
                font-size: 1.25rem;
                color: var(--White-color);
                margin-bottom: .75rem;
            }
    
            .role__name{
                color: var(--White-color);
                margin-bottom: var(--mb-0-5);
            }
    
            .card__description{
                font-weight: 500;
                margin-bottom: 1.75rem;
                color: var(--White-color);
                font-weight: 300;
                line-height: 1.2;
            }
        }
        
        .card__shadow {
            width: 200px;
            height: 200px;
            border-radius: 50%;
            position: absolute;
            top: 3.75rem;
            left: 0;
            right: 0;
            margin-inline: auto;
            filter: blur(45px);
        }
    }

  



}

