:root {
  /* ========== Color ========== */
  --Primary-color: hsl(199, 100%, 33%);
  --Secondary-color: hsl(187, 85%, 43%);
  --Gradient-color: linear-gradient(to right, var(--Secondary-color), var(--Primary-color));
  --White-color: #FFEEF4;
  --Black-color: #321E1E;
  --Text-color: #472D2D;
  --Body-color: #E7F6F2;
  --Card-bg: #D2E9E9;
  --Card-bg-1: #A7D397;
  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
  /*========== Margenes Bottom ==========*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-25: 1.25rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  /*========== Shadow ==========*/
  --shadow-1: -4px 8px 24px hsla(0, 0%, 0%, 0.25);
  /*========== z index ==========*/
  --z-fixed: 100;
}

.cards__middle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.cards__middle .card__middle {
  width: 180px;
  height: 254px;
  border-radius: 10px;
  background: var(--Card-bg-1);
  box-shadow: var(--shadow-1);
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  margin-bottom: var(--mb-2);
  padding-left: 20px;
  padding-right: 20px;
}
.cards__middle .card__middle .icon {
  width: 100px;
  height: 85px;
  color: var(--Text-color);
}
.cards__middle .card__middle .card__middle__content {
  line-height: 1.2;
}
.cards__middle .card__middle .card__middle__content h1 {
  padding-top: var(--mb-1);
  font-size: var(--mb-1-25);
  font-weight: var(--font-semi-bold);
  color: var(--Text-color);
}
.cards__middle .card__middle .card__middle__content p {
  color: var(--Text-color);
  font-size: 14px;
}

@media screen and (min-width: 768px) {
  .cards__middle {
    margin-top: 5rem;
    display: grid;
    grid-template-columns: repeat(2, 250px);
    justify-content: center;
    align-items: center;
    width: 100%;
    grid-gap: 3rem;
  }
}
@media screen and (min-width: 1024px) {
  .cards__middle {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    z-index: 55;
    margin-bottom: 0;
  }
}/*# sourceMappingURL=middle.css.map */