:root {
  /* ========== Color ========== */
  --Primary-color: hsl(199, 100%, 33%);
  --Secondary-color: hsl(187, 85%, 43%);
  --Gradient-color: linear-gradient(to right, var(--Secondary-color), var(--Primary-color));
  --White-color: #FFEEF4;
  --Black-color: #321E1E;
  --Text-color: #472D2D;
  --Body-color: #E7F6F2;
  --Card-bg: #D2E9E9;
  --Card-bg-1: #A7D397;
  --container-color: hsl(212, 42%, 20%);
  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
  /*========== Margenes Bottom ==========*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-25: 1.25rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  /*========== Shadow ==========*/
  --shadow-1: -4px 8px 24px hsla(0, 0%, 0%, 0.25);
  /*========== z index ==========*/
  --z-fixed: 100;
  /*========== Hover overlay ==========*/
  --img-transition: 0.3s;
  --img-hidden: hidden;
  --img-scale: scale(1.1);
}

.footer {
  background-color: var(--container-color);
}
.footer .footer__container {
  display: grid;
  row-gap: 5rem;
}
.footer .footer__content {
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  row-gap: 2rem;
}
.footer .footer__title {
  font-size: var(--h3-font-size);
  margin-bottom: var(--mb-0-5);
  color: var(--White-color);
  font-weight: 500;
}
.footer .footer__subtitle {
  font-size: var(--h3-font-size);
  margin-bottom: var(--mb-1);
  color: var(--White-color);
  font-weight: 500;
}
.footer .footer__description {
  margin-bottom: var(--mb-2);
  color: var(--White-color);
}
.footer .footer__social {
  font-size: 1.25rem;
  color: var(--White-color);
  margin-right: var(--mb-1-25);
}
.footer .footer__item {
  margin-bottom: var(--mb-0-75);
}
.footer .footer__link {
  color: var(--White-color);
}
.footer .footer__link:hover {
  color: var(--Primary-color);
}
.footer .footer__rights {
  display: flex;
  flex-direction: column;
  row-gap: 1.5rem;
  text-align: center;
}
.footer .footer__copy {
  font-size: var(--small-font-size);
  color: var(--White-color);
}
.footer .footer__terms-link {
  font-size: var(--small-font-size);
  color: var(--White-color);
}
.footer .footer__terms-link:hover {
  color: var(--Primary-color);
}
.footer .footer__terms {
  display: flex;
  -moz-column-gap: 1.5rem;
       column-gap: 1.5rem;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  .footer .footer__rights {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media screen and (min-width: 1200px) {
  .footer .footer__container {
    width: 1124px;
    margin: auto;
  }
}/*# sourceMappingURL=footer.css.map */