:root {
  /* ========== Color ========== */
  --Primary-color: hsl(199, 100%, 33%);
  --Secondary-color: hsl(187, 85%, 43%);
  --Gradient-color: linear-gradient(to right, var(--Secondary-color), var(--Primary-color));
  --White-color: #FFEEF4;
  --Black-color: #321E1E;
  --Text-color: #472D2D;
  --Body-color: #E7F6F2;
  --Card-bg: #D2E9E9;
  --Card-bg-1: #A7D397;
  --container-color: hsl(212, 42%, 20%);
  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
  /*========== Margenes Bottom ==========*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-25: 1.25rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  /*========== Shadow ==========*/
  --shadow-1: -4px 8px 24px hsla(0, 0%, 0%, 0.25);
  /*========== z index ==========*/
  --z-fixed: 100;
  /*========== Hover overlay ==========*/
  --img-transition: 0.3s;
  --img-hidden: hidden;
  --img-scale: scale(1.1);
}

.contact {
  padding-top: 6rem;
  padding-bottom: 5rem;
}
.contact .contact__data h1 {
  font-size: var(--mb-2);
  font-weight: var(--font-semi-bold);
  color: var(--Text-color);
  margin-bottom: var(--mb-0-75);
  padding: 10px;
  text-align: center;
}
.contact .contact__box {
  position: relative;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 5fr 4fr;
  grid-template-areas: "form info" "form map";
  grid-gap: 20px;
  padding: 40px;
  margin: auto;
  width: 82%;
}
.contact .contact__box .box {
  padding: 40px;
  background-color: var(--Card-bg);
  box-shadow: var(--shadow-1);
  border-radius: 10px;
}
.contact .contact__box .contact__form {
  grid-area: form;
}
.contact .contact__box .contact__form h3 {
  font-size: 1.3rem;
  margin-bottom: var(--mb-0-75);
  color: var(--Text-color);
  display: flex;
  justify-content: start;
  align-items: center;
}
.contact .contact__box .contact__form form .form__box {
  position: relative;
  width: 80%;
}
.contact .contact__box .contact__form form .form__box .form__box, .contact .contact__box .contact__form form .form__box .row50 {
  display: flex;
  gap: 20px;
}
.contact .contact__box .contact__form form .form__box .form__box .form__input, .contact .contact__box .contact__form form .form__box .row50 .form__input {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  width: 50%;
}
.contact .contact__box .contact__form form .form__box .form__box .form__input span, .contact .contact__box .contact__form form .form__box .row50 .form__input span {
  color: var(--Text-color);
  font-weight: 500;
}
.contact .contact__box .contact__form form .form__box .form__box .form__input input, .contact .contact__box .contact__form form .form__box .row50 .form__input input {
  padding: 10px;
  font-size: 0.9rem;
  outline: none;
  color: var(--Text-color);
  border-radius: 10px;
  border: 0px solid --Black-color;
}
.contact .contact__box .contact__form form .form__box .form__box, .contact .contact__box .contact__form form .form__box .row100 {
  display: flex;
  gap: 20px;
}
.contact .contact__box .contact__form form .form__box .form__box .form__input, .contact .contact__box .contact__form form .form__box .row100 .form__input {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  width: 100%;
}
.contact .contact__box .contact__form form .form__box .form__box .form__input span, .contact .contact__box .contact__form form .form__box .row100 .form__input span {
  color: var(--Text-color);
  font-weight: 500;
}
.contact .contact__box .contact__form form .form__box .form__box .form__input input, .contact .contact__box .contact__form form .form__box .row100 .form__input input {
  padding: 10px;
  font-size: 0.9rem;
  outline: none;
  color: var(--Text-color);
}
.contact .contact__box .contact__form form .form__box .form__box .form__input textarea, .contact .contact__box .contact__form form .form__box .row100 .form__input textarea {
  padding: 10px;
  font-size: 0.9rem;
  outline: none;
  color: var(--Text-color);
  resize: none;
  min-height: 220px;
  border-radius: 10px;
}
.contact .contact__box .contact__form form .form__box .form__box .form__input .input__btn, .contact .contact__box .contact__form form .form__box .row100 .form__input .input__btn {
  background-color: var(--Card-bg-1);
  border: none;
  color: var(--Text-color);
  font-size: 1rem;
  max-width: 120px;
  font-weight: 500;
  cursor: pointer;
  border-radius: 5px;
}
.contact .contact__box .contact__form form .form__box .form__box .form__input .input__btn:hover, .contact .contact__box .contact__form form .form__box .row100 .form__input .input__btn:hover {
  background-color: var(--Primary-color);
  color: var(--White-color);
}
.contact .contact__box .contact__form form .form__box .form__box .form__input::-moz-placeholder, .contact .contact__box .contact__form form .form__box .row100 .form__input::-moz-placeholder {
  color: #D2E9E9;
}
.contact .contact__box .contact__form form .form__box .form__box .form__input::placeholder, .contact .contact__box .contact__form form .form__box .row100 .form__input::placeholder {
  color: #D2E9E9;
}
.contact .contact__box .contact__info {
  grid-area: info;
  background: var(--container-color);
}
.contact .contact__box .contact__info h3 {
  font-size: 1.3rem;
  margin-bottom: var(--mb-0-75);
  color: var(--White-color);
}
.contact .contact__box .contact__info .info__data div {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.contact .contact__box .contact__info .info__data div span {
  min-width: 40px;
  height: 40px;
  color: var(--White-color);
  background-color: var(--Primary-color);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.contact .contact__box .contact__info .info__data div p {
  color: var(--White-color);
  padding-left: 10px;
}
.contact .contact__box .contact__info .info__data div a {
  color: var(--White-color);
  padding-left: 10px;
}
.contact .contact__box .contact__info .info__data .social__media {
  margin-top: 40px;
  display: flex;
}
.contact .contact__box .contact__info .info__data .social__media li {
  margin-right: 15px;
}
.contact .contact__box .contact__info .info__data .social__media li a {
  color: var(--White-color);
  font-size: 20px;
}
.contact .contact__box .contact__info .info__data .social__media li a:hover {
  color: var(--Primary-color);
}
.contact .contact__box .contact__map {
  grid-area: map;
  margin: 0;
  padding: 0;
}
.contact .contact__box .contact__map iframe {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
@media (max-width: 991px) {
  .contact .contact__box {
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-template-areas: "form" "info" "map";
  }
  .contact .contact__box .form__box, .contact .contact__box .row50 {
    display: flex;
    gap: 0;
    flex-direction: column;
    justify-content: center;
  }
  .contact .contact__box .contact__form form .form__box .form__box .form__input {
    width: 100%;
  }
  .contact .contact__box .contact__form form .form__box .row50 .form__input {
    width: 100%;
  }
  .contact .contact__box .contact__map {
    min-height: 300px;
  }
}
@media (max-width: 595px) {
  .contact .contact {
    margin: auto;
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .contact .contact__form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .contact .contact__form h3 {
    padding-bottom: var(--mb-1-5);
  }
  .contact .contact__form form {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
}/*# sourceMappingURL=contact.css.map */